import React, { Component } from "react";
import { connect } from "react-redux";
import "./launchpage.css";
import Map from "react-icons/lib/fa/map";
import WikipediaW from "react-icons/lib/fa/wikipedia-w";
import ClockO from "react-icons/lib/fa/clock-o";
import SocialYoutubeOutline from "react-icons/lib/io/social-youtube-outline";

import Card from "../ui/Card";
import LabelValue from "../ui/LabelValue";
import Button from "../ui/Button";
import Preloader from "../ui/Preloader";
import CountDown from "../ui/CountDown";

import api from "../apis/LaunchLibrary";

import format from "date-fns/format";
// import "./launchpage.css";
class LaunchPage extends Component {
  state = {
    launch: undefined
  };

  componentDidMount() {
    window.scrollTo(0, 0);

    window.addEventListener("resize", this.handleResize);

    api.launch(this.props.match.params.id).then(data => {
      const launch = data.launches[0];
      if (!this.getLaunchFromList()) {
        this.setState({ launch });
      }
    });
  }

  handleResize = () => {
    if (!this.streamIframe) {
      this.streamIframe = document.getElementById("ytfr");
    }
    if (!this.streamIframe) {
      return;
    }
    requestAnimationFrame(() => {
      this.streamIframe.width = document.body.clientWidth;
      this.streamIframe.height = Math.min(
        document.body.clientWidth * 9 / 16,
        600
      );
    });
  };

  componentWillUnmount() {
    window.removeEventListener("resize", this.handleResize);
  }

  getLaunchFromList = () => {
    const launchId = this.props.match.params.id;
    const l = this.props.launches.find(
      launch => parseInt(launch.id, 10) === parseInt(launchId, 10)
    );
    return l;
  };

  render() {
    const l = this.getLaunchFromList() || this.state.launch;
    return (
      <div>
        {l && l.vidURLs[0] ? <Stream url={l.vidURLs[0]} /> : ""}
        {l
          ? <div className="launch-container">

                <Card title="Launch">
                  <LabelValue
                    label="Countdown"
                    value={<CountDown target={new Date(l.net)} />}
                  />
                  <LabelValue
                    medium
                    label={
                      <div>
                        <ClockO />When
                      </div>
                    }
                    value={format(new Date(l.net), "D MMMM HH:mm")}
                  />

                  {l.vidURLs.map((url, i) =>
                    <Button key={i} href={url}>
                      <SocialYoutubeOutline />Video
                    </Button>
                  )}
                </Card>

                {l.missions.map(m => <Mission key={m.id} mission={m} />)}
                <RocketCard rocket={l.rocket} />
                {l.rocket.agencies.map(a => <Agency key={a.id} agency={a} />)}
                {l.location.pads.map(p => <Pad key={p.id} pad={p} />)}

            </div>
          : <div className="launch-container">
              <Preloader />
            </div>}
      </div>
    );
  }
}

const RocketCard = ({ rocket }) => {
  return (
    <Card title="Rocket">
      <LabelValue label="Name" value={rocket.name} />
      <LabelValue
        label="Made by"
        medium
        value={rocket.agencies.map(a => a.name).join(", ")}
      />

      <Button href={rocket.infoURL}>Website</Button>
      <Button href={rocket.wikiURL}>
        <WikipediaW />
        Wiki
      </Button>
    </Card>
  );
};

const Stream = ({ url }) => {
  if (url.indexOf("youtube") > -1) {
    const id = url.split("=").pop();
    return (
      <div className="Stream">
        <iframe
          title="youtube stream"
          width={document.body.clientWidth}
          height={Math.min(document.body.clientWidth * 9 / 16, 600)}
          src={`https://www.youtube.com/embed/${id}`}
          frameBorder="0"
          id="ytfr"
          allowFullScreen
        />
      </div>
    );
  }
  return <div />;
};

const Mission = ({ mission }) => {
  return (
    <Card title="Mission">
      <LabelValue label="Name" value={mission.name} />
      <LabelValue label="Type" value={mission.typeName} medium />
      <p>
        {mission.description}
      </p>
    </Card>
  );
};

const Agency = ({ agency }) => {
  return (
    <Card title="Agency">
      <LabelValue label="Name" value={agency.name} />
      <LabelValue label="Country" value={agency.countryCode} medium />

      <Button href={agency.infoURL}>Website</Button>
      <Button href={agency.wikiURL}>
        <WikipediaW />
        Wiki
      </Button>
    </Card>
  );
};

const Pad = ({ pad }) => {
  return (
    <Card title="Pad">
      <LabelValue label="Name" value={pad.name} />

      <Button href={pad.mapURL}>
        <Map />Map
      </Button>
      <Button href={pad.wikiURL}>
        <WikipediaW />
        Wiki
      </Button>
    </Card>
  );
};

const mapStateToProps = ({ data: { launches } }) => ({
  launches
});

export default connect(mapStateToProps)(LaunchPage);



